.newChat{
    width: 35%;
    max-width: 415px;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #EEE;
    display: flex;
    flex-direction: column;
    border-right: #CCC;
    transition: all ease-in-out 1s;
}

.newChat--head {
    display: flex;
    background-color: #00BFA5;
    align-items: center;
    padding: 60px 15px 15px 15px;
}

.newChat--backbutton {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.newChat--headtitle {
    font-size: 19px;
    line-height: 40px;
    color: #FFFFFF;
    flex: 1;
    font-weight: bold;
    margin-left: 20px;
}

.newChat--list {
    flex: 1;
    overflow-y: auto;
}

.newChat--list::-webkit-scrollbar{
    width: 6px;
    height: 6px;

}
.newChat--list::-webkit-scrollbar-thumb {
    background-color: rgba(0,0,0,0.2);
}

.newChat--item {
    display: flex;
    align-items: center;
    padding: 15px;
    cursor: pointer;
}

.newChat--item:hover{
    background-color: #F5F5F5;
}
.newChat--itemavatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 15px;
}

.newChat--itemname {
    font-size: 17px;
    color: #000;
}

